const uni = require('uni').default;




















































































import app from "../../common/common.js";
import mtFooter from "../../components/footer.vue";
export default {
	components:{
		mtFooter
	},
	data: {
		pageLoad: false,
		pageHide:false,
		pageData: {}
	},
	onLoad: function (option) {
		uni.setNavigationBarTitle({
			title:"个人中心",
		})
		this.getPage();
	},
	onShow:function(){
		if(this.pageHide){
			this.pageHide=false;
			this.getPage();
		}			
	},
	onHide:function(){
		this.pageHide=true;
	},
	methods: {
		gourl: function (url) {
			uni.navigateTo({
				url: url,
			})
		},
		getPage: function () {
			var that = this;
			uni.request({
				url: app.apiHost + "?m=user&ajax=1",
				data: {
					authcode: app.getAuthCode()
				},
				success: function (res) {
					if(res.data.error==1000){
						uni.navigateTo({
							url:"/pages/login/index",
						})
					}else{
						that.pageLoad = true;
						that.pageData = res.data.data;
					}
					

				}
			})
		}
	},
}



